
.flag-img {
	border-radius: 5px;
	max-width: 50px;
	width: 50px;
	height: 40px;	
}

.country-row {
	height: 64px;
}

.country-row:hover {
	cursor: pointer;
	transform: scale(1.02);
}

@media (max-width: 640px) {
	.flag-img {
		width: 45px;
		height: 35px;
	}
	.text-row {
		font-size: 12px;
	}
}